                          <div class="card-text">
                              Thanks for your interest in <strong>ESTER: A Machine Reading Comprehension Dataset for Event Semantic Relation Reasoning</strong>. Our <a href="https://arxiv.org/abs/2104.08350">paper</a> will appear on 
                              the proceedings of EMNLP'21 conference and the reproduction code is available <a href="https://github.com/PlusLabNLP/ESTER">here</a>.
                          <hr>
                          </div>

                          <div class="card-text">

                              <h3>Introduction</h3>
                              <p> Stories and narratives are often based on a sequence of events. Understanding how these events are semantically or logically related to each other is essential for reading comprehension.
                              Unlike previous relation extraction datasets such as <a href="https://www.cse.msu.edu/~kordjams/papers/Glavasetal.LREC2014.pdf">HiEve</a> and <a href="https://aclanthology.org/W16-5706.pdf">RED</a>, 
                              we formulate event semantic reasoning as a QA task. Annotators still need to identify event (triggers) in a given passage, 
                              but instead of creating (event, event, relation) triplets as samples, now they
                              <ol>
                                  <li>  <span style='color:Tomato'> <strong>use natural language to query about event relations </strong> </span> </li>
                                  <li> <span style='color:Tomato'> <strong>answer with complete meaning spans in the passage </strong> </span> </li>
                              </ol>
                              </p>
                              <hr>

                              <h3>Event Semantic Relations</h3>
                              <p>We study five event semantic relations. Below, we provide their brief definitions followed by an illustrating example. Formal definitions can be found in the paper.</p>
                              <ol style='list-style-type:upper-roman;font-size:18px'>
                                <li><span style='font-size:18px; color:Tomato'><strong>Causal Relation:</strong></span> Event A {causes / leads to / results in} event B in the passage. In other words, <strong>according to the passage</strong>, if event A happens, event B will definitely happen.<br></li>
                                <button type="button" class="btn btn-info" data-toggle="collapse" data-target="#causal-collapse" v-on:click="toggle_causal">{{causalTitle}}</button>
                                <div id="causal-collapse" class="collapse"> 
                                <div style="margin-left: 1em;">
                                <em><span>[Passage] Defense Secretary Dick Cheney is <mark>urging</mark> Bush to <mark>order</mark> a national callup of armed forces reserves for active duty because of the <mark>drain</mark> on units <mark>sending</mark> soldiers abroad.</span></em><br>
                                <span style='color:Dodgerblue;'>[Question] What made Dick Cheney urge Bush to order a callup of armed forces reserves?</span><br>
                                <span style='color:DarkOrange;'>[Answer] the drain on units sending soldiers abroad.</span><br>
                                <span style='color:black;'>[Explanation] In this example, it's clear that "the drain on units sending soldiers abroad" is the only cause of Dick Cheneys suggestions for Bush.</span>
                                </div>
                                </div>

                                <br><br>
                                
                                <li><span style='font-size:18px; color:Tomato'><strong>Indicative Conditional Relation:</strong></span> Event A is indicative conditional of event B if event A facilitates event B.</li>
                                <button type="button" class="btn btn-info" data-toggle="collapse" data-target="#condition-collapse" v-on:click="toggle_condition">{{conditionTitle}}</button>
                                <div id="condition-collapse" class="collapse"> 
                                <div style="margin-left: 1em;">
                                <em><span>[Passage] Saddam's message called for an international conference to be <mark>convened</mark> to discuss an Iraqi <mark>withdrawal</mark> from Kuwait. In exchange, the buildup of U.S. forces in the region would <mark>halt</mark>.</span></em><br>
                                <span style='color:DodgerBlue;'>[Question] What could be expected to happen after Saddam convened an international conference?</span><br>
                                <span style='color:DarkOrange;'>[Answer] the buildup of U.S. forces in the region would halt.</span><br>
                                <span style='color:black;'>[Explanation] We can infer from the passage that the "international conference" will not necessarily result in the halt of "the buildup of U.S. forces", or it's merely a condition that could facilitate the latter event. Therefore, (convened and halt) forms an indicative conditional relation.</span><br>
                                </div>
                                </div>
                                <br><br>

                                <li><span style='font-size:18px; color:Tomato'><strong>Counterfactual Conditional Relation:</strong></span> Event A may happen if Event B <strong>didnt</strong> happen.<br></li>
                                <button type="button" class="btn btn-info" data-toggle="collapse" data-target="#counterfactual-collapse" v-on:click="toggle_counterfactual">{{counterfactualTitle}}</button>
                                <div id="counterfactual-collapse" class="collapse"> 
                                <div style="margin-left: 1em;">
                                <em><span>[Passage] Program <mark>trading</mark> also causes the Nasdaq Composite Index to <mark>lose</mark> ground against other segments of the stock market. Because of program trading it is more difficult to <mark>trade</mark> many OTC stocks without sharp price moves, a condition known as illiquidity.
</span></em><br>
                                <span style='color:DodgerBlue;'>[Question] What is going to happen if there is no program trading?</span><br>
                                <span style='color:DarkOrange;'>[Answer] trade many OTC stocks without sharp price moves. </span><br>
                                <span style='color:black;'>[Explanation] According to the passage, program trading happened or is happening. In the question, we assume it didn't happen or is not happening and ask what would happen. This example aligns well with our definition of Counterfactual Conditional relation.
</span></div></div>
                                <br><br>
                                <li><span style='font-size:18px; color:Tomato'><strong>Sub-event Relation:</strong></span> Sub-event relation occurs when there is a complex event that contains several sub-events as its components. 
                                <br>
                                <button type="button" class="btn btn-info" data-toggle="collapse" data-target="#subevent-collapse" v-on:click="toggle_subevent">{{subeventTitle}}</button>
                                <div id="subevent-collapse" class="collapse"> 
                                For example, having dinner in a restaurant is a complex event, and its sub-events include activities such as ordering meals, eating, and paying the check. We show a full example below.<br>
                                <div style="margin-left: 1em;">
                                <em><span>[Passage] Professor Peng is <mark>teaching</mark> CS544 this semester. She <mark>gives</mark> two lectures every week, <mark>holds</mark> office hours, and <mark>grades</mark> exams. Her <mark>effort</mark> as an instructor has been highly <mark>recognized</mark> by the school.</span></em><br>
                                <span style='color:DodgerBlue;'>[Question] What do Professor Peng's teaching tasks include?</span><br>
                                <span style='color:DarkOrange;'>[Answer] gives lectures; holds office hours; grades exams.</span><br>
                                <span style='color:black;'>[Explanation] Teaching is a complex event that includes gives lectures, holds office hours and grades exams. Therefore, these three three event pairs, (teaching, gives), (teaching, holds) and (teaching grades) form sub-event relations.</span>
                                </div>
                                </div>
                                </li>
                                
                                <br>

                                <li><span style='font-size:18px; color:Tomato'><strong>Coreference Relation:</strong></span> Some event actions can be considered as the same event depending on their event arguments and their context. Coreference events <strong>MUST share the same event arguments</strong>, i.e. subject, object, time, and location.</li>
                                <button type="button" class="btn btn-info" data-toggle="collapse" data-target="#coreference-collapse" v-on:click="toggle_coreference">{{coreferenceTitle}}</button>
                                <div id="coreference-collapse" class="collapse"> 

                                <div style="margin-left: 1em;">
                                <em><span>[Passage] China <mark>aims</mark> to <mark>keep</mark> the registered urban unemployment rate below 5 percent in the coming five years, with additional 45 million people being employed. China's Minister of Labor and Social Security Tian Chengping said recently that to <mark>achieve</mark> the goal, adjustments will be <mark>made</mark> to <mark>enlarge</mark> the scope of employment.</span></em><br>
                                <span style='color:DodgerBlue;'>[Question] What is the goal that China would like to achieve?</span><br>
                                <span style='color:DarkOrange;'>[Answer] keep the registered urban unemployment rate below 5 percent</span><br>
                                <span style='color:black;'>[Explanation] In this context, <mark>keep</mark> unemployment rate below 5 percent and <mark>achieve</mark> the goal refer to the same event. This is because for the two event actions words <mark>keep</mark> and <mark>achieve</mark>, their subject are both China and their objects are "unemployment rate under 5 percent" and "goal" respectively. However, "unemployment rate under 5 percent" and "goal" are the same. So this pair of events share the same event arguments and are mutually replacable. </span>
                                </div>
                                </div>
                                <br>               
                              </ol>

