                              <h3> Key Challenges </h3>
                                Apart from the difficulty for machines to reason about event semantic relations, <strong>ESTER</strong> adds two challenges for MRC,
                                <ol>
                                    <li> <span style='color:Tomato'> <strong>The flexibility of natural language queries tests models' robustness. </strong></span> For example, in the example question for Sub-event relation above, we can modify the question to be "What do Professor Peng's teaching tasks include in classroom?" 
                                    Then only the first answer "gives lectures" is valid. A good model can detect subtle additions of event arguments in questions and adjust answer accordingly. </li>
                                    <li> <span style='color:Tomato'> <strong>Models need to predict complete and meaningful answer spans. </strong></span> Partial answer overlaps or scatterred token predictions are penalized. </li>
                                </ol> 
